@import "../node_modules/tinper-bee-core/scss/minxin-variables";
@import "../node_modules/tinper-bee-core/scss/minxin-mixins";
@import "../node_modules/bee-form-control/src/FormControl.scss";
@import "../node_modules/bee-checkbox/src/Checkbox.scss";
@import "../node_modules/bee-select/src/Select.scss";
@import "../node_modules/bee-popover/src/Popover.scss";
@import  "../node_modules/bee-pagination/src/Pagination.scss";
@import "../node_modules/bee-dropdown/src/Dropdown.scss";
@import "../node_modules/bee-menus/src/Menu.scss";
@import "../node_modules/bee-table/src/Table.scss";
$icon-color:#505F79;
.u-grid{
    th{
        .drop-menu{
            .uf{
                font-size: 12px;
                visibility: hidden;
                color:$icon-color;
                font-weight: bold;
                // margin-left: 7px;
                cursor: pointer;
            }
        }
        .drop-menu {
            // width: calc(100% - 18px) !important;
            // text-overflow: ellipsis;
            // white-space: nowrap;
            display: inline-block;
            // vertical-align: middle;
            // overflow: hidden;
            // width:100%;
            i.uf{
                padding-left: 4px;
            }
            .drop-menu-title{
                text-overflow: ellipsis;
                white-space: nowrap;
                display: inline-block;
                vertical-align: middle;
                overflow: hidden;
                max-width: calc( 100% - 21px);
            }
        }
        .cansort.drop-menu {
                max-width: calc(100% - 32px) !important;
            
        }
        &:last-child{
            .cansort.drop-menu {
                width: calc(100% - 64px) !important;
            
            }
        }
        .bee-table-column-sorter {
            margin-left: 0px;
            // margin-top:4px;
            i{
                padding:0px;
            }
        }
        
        .bee-table-column-sorter>.bee-table-column-sorter-up {
            width: auto;
        }
        &:hover{
            .drop-menu{
                // background: #ccc;
                .uf{
                        visibility: visible;
                }
            }
        }
        ::last-child{
            overflow: hidden;
          }
          
    }
    .u-table-fixed-left {
        th{
            &:last-child{
                .cansort.drop-menu {
                    width: calc(100% - 32px) !important;
                
                }
            }
        }
       
    }
    .u-checkbox{
        margin:0px;
    }
    .u-pagination {
        
        overflow: hidden;
        margin-bottom: 8px;
        .u-pagination-list{
            margin-left: 0px;
        }
        .data_per_select .u-select {
            width: 60px;
        }
        &.center{
            display: flex;
            justify-content: center;
        }
        &.right{
            display: flex;
            justify-content: flex-end;;
        }
        
    } 
}

.u-table-filter-column-pop-cont-item{
    .drop-menu{
        .uf{
            visibility: hidden;
        }
    }
}



.u-table-thead th .bee-table-column-sorter>.bee-table-column-sorter-down, .u-table-thead th .bee-table-column-sorter>.bee-table-column-sorter-flat, .u-table-thead th .bee-table-column-sorter>.bee-table-column-sorter-up {
    width:auto !important;
}

.u-dropdown.grid-menu{
    z-index: 1800;
}


